<template>
  <base-facility>
    <template v-slot:title>{{ $route.name }}</template>
    <template v-slot:actions>
      <create-facility-setting-modal>{{  $t('buttons.createNewFacility') }}</create-facility-setting-modal>
    </template>
    <template v-slot:default>
      <v-card class="v-card-responsive-table pa-5" shaped tile >
        <v-card-title class="grey--text mt-n5" v-once>
          <v-breadcrumbs
            large
            :items="breadcrumbItems"
            class="ma-0 pa-0 custom-breadcrumb"
            divider=">"
          ></v-breadcrumbs>
        </v-card-title>
        <v-simple-table class="custom-table d-flex mt-n4">
          <template v-slot:default>
            <thead>
            <tr>
              <th class="text-left">
                {{  $t('facility.idProperty') }}
              </th>
              <th class="text-left">
                {{  $t('facility.nameProperty') }}
              </th>
              <th></th>
              <th class="text-left">
                {{  $t('facility.facilitiesEdit') }}
              </th>
            </tr>
            </thead>
            <tbody>
            <tr
              v-for="item in facilities"
              :key="item.id"
            >
              <td>{{ item.id }}</td>
              <td class="font-weight-medium">{{ item.name }}</td>
              <td>
                <v-btn
                  color="secondary"
                  small
                  link
                  :to="'settings/' + item.id + '/room-types'"
                >{{  $t('facility.roomTypeSettings') }}</v-btn>
              </td>
              <td class="text-center">
                <v-btn
                  color="white"
                  elevation="2"
                  x-small
                  fab
                  :to="'/facility/settings/12/edit'"
                  target="_blank"
                >
                  <v-icon color="secondary">
                    mdi-pencil
                  </v-icon>
                </v-btn>
              </td>
            </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card>
    </template>
  </base-facility>
</template>

<script>
import BaseFacility from '@/components/Facilities/BaseFacility'
import CreateFacilitySettingModal from '@/components/Facilities/Settings/CreateModal'

export default {
  name: 'FacilitySettings',
  data () {
    return {
      facilities: [],
      breadcrumbItems: [
        {
          text: this.$t('facility.data.breadcrumbs.facilitySettings'),
          disabled: true,
          href: ''
        }
      ]
    }
  },
  mounted () {
    this.initFacilities()
  },
  methods: {
    initFacilities () {
      const facilityName = this.$t('facility.data.facilities')
      for (let i = 0; i < 5; i++) {
        this.facilities.push({
          id: Math.floor(Math.random() * 90000) + 10000,
          name: facilityName
        })
      }
    }
  },
  components: {
    CreateFacilitySettingModal,
    BaseFacility
  }
}
</script>

<style scoped lang="scss">

</style>
