<template>
  <v-dialog
    v-model="isModalOpen"
    persistent
    max-width="800px"
  >
    <template v-slot:activator="{ on, attrs }">
      <v-btn
        color="primary float-right mt-2"
        dark
        v-bind="attrs"
        v-on="on"
      >
        <slot />
      </v-btn>
    </template>
    <v-card class="pa-4">
      <v-form
        @submit.prevent="onSubmit"
        class="custom-form"
        ref="form"
      >
        <v-card-title>
          <span class="headline">{{ title }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <form-row>
              <template v-slot:key>
                <label
                  class="custom-form-label"
                  :for="'facility-type'"
                >
                  {{  $t('facility.facilityType') }}
                </label>
              </template>
              <template v-slot:value>
                <v-row>
                  <v-col cols="12" md="5" lg="5">
                    <v-select
                      id="facility-type"
                      :items="items"
                      :rules="rules.facilityType"
                      dense
                      outlined
                    ></v-select>
                  </v-col>
                  <v-col cols="12" md="7" lg="7">
                    <p>
                      {{ $t('facility.pointForFingerType') }}
                    </p>
                  </v-col>
                </v-row>
              </template>
            </form-row>
            <form-row>
              <template v-slot:key>
                <label
                  class="custom-form-label"
                  :for="'facility-name'">
                  {{  $t('facility.facilityName') }}
                </label>
              </template>
              <template v-slot:value>
                <v-row>
                  <v-col cols="12" md="5" lg="5">
                    <v-text-field
                      id="facility-name"
                      :rules="rules.facilityName"
                      dense
                      outlined
                    ></v-text-field>
                  </v-col>
                </v-row>
              </template>
            </form-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="errorButton"
            dark
            @click="isModalOpen = false"
          >
            {{  $t('facility.cancel') }}
          </v-btn>
          <v-btn
            type="submit"
            color="primary"
            dark
          >
            {{  $t('facility.saveAndCreate') }}
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
import FormRow from '@/components/Form/FormRow'
export default {
  name: 'CreateFacilitySettingModal',
  components: { FormRow },
  data () {
    return {
      title: this.$t('facility.creatingNewFacility'),
      isModalOpen: false,
      items: this.$t('facility.data.facilityTypes').map((x, i) => ({
        text: x,
        value: i + 1
      })),
      rules: {
        facilityType: [
          v => !!v || this.$t('rules.typeRequired')
        ],
        facilityName: [
          v => !!v || this.$t('rules.nameIsRequired'),
          v => (v && v.length >= 1) || this.$t('rules.minimumNCharacter', { value: 1 })
        ]
      }
    }
  },
  methods: {
    async onSubmit () {
      if (!this.$refs.form.validate()) {
        return
      }

      this.isModalOpen = false

      const routeData = this.$router.resolve({ path: '/facility/settings/12/edit' })
      window.open(routeData.href, '_blank')
    }
  }
}
</script>

